import React from "react";
import UrlEncoder from "./pages/UrlEncoder/UrlEncoder";
import Dock, { IDockItem } from './components/Dock/Dock';
import Menu from './components/Menu/Menu';
import "./App.css";
import { Route, RouteComponentProps, Switch } from "react-router";
import { Stack, StackItem } from "@fluentui/react";

interface IAppState {
    isMenuOpen: boolean;
}

interface IAppItem extends IDockItem {
    component: React.ComponentType<RouteComponentProps<any>> | React.ComponentType<any>;
}

export default class App extends React.Component<any, IAppState> {
    private _DockItems: IAppItem[];

    public constructor(props: any) {
        super(props);
        this._DockItems = [
            {
                title: "Home",
                path: "/",
                iconName: "Home",
                onClick: this._onDockItemClicked,
                component: () => (<></>)
            },
            {
                title: "URL Encoder",
                path: "/tools/url-encoder",
                iconName: "ChangeEntitlements",
                onClick: this._onDockItemClicked,
                component: UrlEncoder
            }
        ];
        this.state = {
            isMenuOpen: false
        };
    }

    private _onMenuClicked = () => {
        this.setState({ isMenuOpen: !this.state.isMenuOpen });
    }

    private _onDockItemClicked = (item: IDockItem) => {
        window.location.hash = `#${item.path}`;
    }

    public render() {
        return <div className="App">
            <Stack verticalFill>
                <StackItem className="AppContent" grow>
                    <Switch>
                        {this._DockItems.map(i => {
                            return <Route key={i.path} exact path={i.path} component={i.component} />;
                        })}
                    </Switch>
                </StackItem>
                {this.state.isMenuOpen && <Menu />}
                <StackItem className="AppDock">
                    <Route path="*" render={() => (<Dock dockItems={this._DockItems} onMenuClicked={this._onMenuClicked} />)} />
                </StackItem>
            </Stack>
        </div>;
    }
}
